<template>
    
    <div class="modal"  v-if="$store.state.selectedProduct">
        
        <div class="backdrop" @click="closeModal()"></div>
        <transition name="fade">
            <div class="article">
                <div class="article__img_container">
                    <img :src="$store.state.selectedProduct.images[0].path" height="400" alt="">
                </div>
                <div class="article__data">
                    <div class="top">
                        <img src="../../assets/icons/heart.svg" style="width: 100px; height: 100px">
                        <div class="article__data-container">
                            <!-- <span>худи</span> -->
                            <p>{{$store.state.selectedProduct.name}}</p>
                        </div>
                    </div>
                    <div class="bottom">
                        <div class="btns">
                            
                            <button class="btn" v-for="size of $store.state.selectedProduct.sizes" :key="size.id" :disabled="size.amount < 1">{{size.sizename}} {{size.amount}}</button>
                            
                        </div>
                        <h4 class="price">{{$store.state.selectedProduct.price}} руб</h4>
                        <button @click="addCart()" v-if="showAddBtn">Добавить в корзину <img src="../../assets/icons/cart.svg"></button>
                    </div>
                </div>
            </div>
        </transition>
    </div>
    
   
</template>

<script>
    export default {
        name: 'ArticleModal',
        data(){
            return {
                showAddBtn : false,
            }
        },
        methods: { 
           
        },
        mounted() {
            this.checkCart();
        },
    }
</script>

